import React from "react";
import { Link } from "react-router-dom";

import "./styles.scss";

export default function Tools({ listMap }: T_ToolsProps) {
  return (
    <div className="Tools">
      <main className="h-full overflow-y-auto">
        <div className="container px-6 mx-auto grid">
          <div className="grid gap-5 sm:gap-10 mb-8 sm:grid-cols-2 xl:grid-cols-4">
            {getToolsListMarkup(listMap)}
          </div>
        </div>
      </main>
    </div>
  );
}

function getToolsListMarkup(listMap: T_ToolsProps["listMap"]) {
  const markup: JSX.Element[] = [];

  listMap.forEach((item, key) => {
    markup.push(
      <Link key={item.name} to={key}>
        <div className="card">
          <div className="cardIcon">
            <img className="cardIconImg" src={item.icon} alt={item.name} />
          </div>
          <div>
            <p className="cardHead">{item.name}</p>
            {item.description && (
              <p className="cardSubHead">{item.description}</p>
            )}
          </div>
        </div>
      </Link>
    );
  });

  return markup;
}

interface T_ToolsProps {
  listMap: Map<
    string,
    {
      name: string;
      icon: string;
      description?: string;
    }
  >;
}
